<template>
  <div class="mail-richtext">
    <quill-editor v-if="type === 0" :content="content" :options="editorOption"></quill-editor>
    <div class="email-dtl" v-else v-html="content"></div>
  </div>
</template>
<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'HelloWorld',
  components: {
    quillEditor
  },
  data () {
    return {
      type: 0,
      content: '',
      editorOption: {
        modules: {
          toolbar: [
          ],
          history: {
            delay: 1000,
            maxStack: 50,
            userOnly: false
          }
        },
        placeholder: ''
      }
    }
  },
  created () {
    window.getValHtml = this.getValHtml
    window.sendValMobile = this.sendValMobile
  },
  methods: {
    // 接收详情数据
    getValHtml (data) {
      this.content = data.html
      this.type = data.type // 0:编辑，1:详情
    },
    // 发送编辑器数据
    sendValMobile (data) {
      if (data === 0) { // Android
        window.android.getContent(this.content)
      } else { // iOS
        window.webkit.messageHandlers.Location.postMessage(this.content)
      }
    }
  }
}
</script>

<style lang="scss">
.mail-richtext {
  .ql-container {
    min-height: 100px;
  }
  .quill-editor {
    height: 100%;
    .ql-toolbar {
      display: none;
    }
    .ql-container {
      border: none;
      .ql-editor {
        min-height: 200px;
      }
    }
  }
}
</style>



